"use client";

import React, { useState } from 'react';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';

export default function CodeEditor() {
  const [code, setCode] = useState('// Escribe tu c√≥digo JavaScript aqu√≠\nconsole.log("¬°Hola, mundo!");');
  const [output, setOutput] = useState('');
  const [error, setError] = useState('');

  const runCode = () => {
    try {
      setError('');
      setOutput('');
      
      // Capture console.log output
      const logs: string[] = [];
      const originalLog = console.log;
      console.log = (...args) => {
        logs.push(args.map(arg => 
          typeof arg === 'object' ? JSON.stringify(arg, null, 2) : String(arg)
        ).join(' '));
      };

      // Execute the code
      const result = new Function(code)();
      
      // Restore original console.log
      console.log = originalLog;
      
      // Set output
      if (logs.length > 0) {
        setOutput(logs.join('\n'));
      } else if (result !== undefined) {
        setOutput(String(result));
      } else {
        setOutput('C√≥digo ejecutado correctamente (sin salida)');
      }
    } catch (err: any) {
      setError(`Error: ${err.message}`);
      // Restore original console.log in case of error
      console.log = console.log;
    }
  };

  const clearCode = () => {
    setCode('// Escribe tu c√≥digo JavaScript aqu√≠\nconsole.log("¬°Hola, mundo!");');
    setOutput('');
    setError('');
  };

  const loadExample = (example: string) => {
    switch (example) {
      case 'variables':
        setCode('// Ejemplo: Variables\nlet nombre = "CodeLearn";\nlet edad = 25;\nlet esEstudiante = true;\n\nconsole.log("Nombre:", nombre);\nconsole.log("Edad:", edad);\nconsole.log("Es estudiante:", esEstudiante);');
        break;
      case 'functions':
        setCode('// Ejemplo: Funciones\nfunction saludar(nombre) {\n  return "¬°Hola, " + nombre + "!";\n}\n\nfunction sumar(a, b) {\n  return a + b;\n}\n\nconsole.log(saludar("Estudiante"));\nconsole.log("5 + 3 =", sumar(5, 3));');
        break;
      case 'arrays':
        setCode('// Ejemplo: Arrays\nlet frutas = ["manzana", "banana", "naranja"];\nlet numeros = [1, 2, 3, 4, 5];\n\nconsole.log("Frutas:", frutas);\nconsole.log("Primera fruta:", frutas[0]);\nconsole.log("N√∫meros:", numeros);\nconsole.log("Suma de n√∫meros:", numeros.reduce((a, b) => a + b));');
        break;
      case 'loops':
        setCode('// Ejemplo: Bucles\nconsole.log("Contando del 1 al 5:");\nfor (let i = 1; i <= 5; i++) {\n  console.log("N√∫mero:", i);\n}\n\nconsole.log("\\nFrutas favoritas:");\nlet frutas = ["üçé", "üçå", "üçä"];\nfrutas.forEach((fruta, index) => {\n  console.log(`${index + 1}. ${fruta}`);\n});');
        break;
      default:
        clearCode();
    }
    setOutput('');
    setError('');
  };

  return (
    <div className="space-y-4">
      {/* Example Buttons */}
      <div className="flex flex-wrap gap-2 mb-4">
        <Button 
          variant="outline" 
          size="sm" 
          onClick={() => loadExample('variables')}
          className="text-xs"
        >
          Variables
        </Button>
        <Button 
          variant="outline" 
          size="sm" 
          onClick={() => loadExample('functions')}
          className="text-xs"
        >
          Funciones
        </Button>
        <Button 
          variant="outline" 
          size="sm" 
          onClick={() => loadExample('arrays')}
          className="text-xs"
        >
          Arrays
        </Button>
        <Button 
          variant="outline" 
          size="sm" 
          onClick={() => loadExample('loops')}
          className="text-xs"
        >
          Bucles
        </Button>
      </div>

      {/* Code Editor */}
      <div className="border rounded-lg overflow-hidden">
        <div className="bg-slate-800 text-white px-4 py-2 text-sm font-medium">
          Editor de C√≥digo JavaScript
        </div>
        <textarea
          className="w-full h-64 p-4 font-mono text-sm border-0 resize-none focus:outline-none focus:ring-2 focus:ring-blue-500"
          value={code}
          onChange={(e) => setCode(e.target.value)}
          placeholder="Escribe tu c√≥digo JavaScript aqu√≠..."
          spellCheck={false}
        />
      </div>

      {/* Control Buttons */}
      <div className="flex gap-2">
        <Button 
          onClick={runCode} 
          className="bg-green-600 hover:bg-green-700 text-white font-semibold"
        >
          ‚ñ∂ Ejecutar C√≥digo
        </Button>
        <Button 
          onClick={clearCode} 
          variant="outline"
          className="text-slate-600 hover:text-slate-900"
        >
          üóë Limpiar
        </Button>
      </div>

      {/* Output Section */}
      {(output || error) && (
        <Card>
          <CardHeader className="pb-2">
            <CardTitle className="text-lg">
              {error ? '‚ùå Error' : '‚úÖ Resultado'}
            </CardTitle>
          </CardHeader>
          <CardContent>
            {error ? (
              <div className="p-3 bg-red-50 border border-red-200 rounded text-red-700 font-mono text-sm">
                {error}
              </div>
            ) : (
              <pre className="p-3 bg-slate-50 border rounded text-slate-800 font-mono text-sm whitespace-pre-wrap overflow-x-auto">
                {output}
              </pre>
            )}
          </CardContent>
        </Card>
      )}

      {/* Tips */}
      <Card className="bg-blue-50 border-blue-200">
        <CardContent className="pt-4">
          <h4 className="font-semibold text-blue-900 mb-2">üí° Consejos:</h4>
          <ul className="text-sm text-blue-800 space-y-1">
            <li>‚Ä¢ Usa <code className="bg-blue-100 px-1 rounded">console.log()</code> para mostrar resultados</li>
            <li>‚Ä¢ Prueba los ejemplos haciendo clic en los botones de arriba</li>
            <li>‚Ä¢ Experimenta modificando el c√≥digo para ver diferentes resultados</li>
            <li>‚Ä¢ Los errores te ayudar√°n a aprender, ¬°no tengas miedo de probar!</li>
          </ul>
        </CardContent>
      </Card>
    </div>
  );
}
